import { useState } from 'react'
import { Canvas } from '@react-three/fiber'
import { OrbitControls } from '@react-three/drei'
import { Physics, useBox, usePlane, useSphere } from '@react-three/cannon'

const BoxTrigger = ({ onCollide, size, position}: any) => {
  const [ref]: any[] = useBox(() => ({
     isTrigger: true,
     args: size,
     position,
     onCollide
  }))

  return (
    <mesh ref={ref} position={position}>
      <boxGeometry args={size} />
      <meshStandardMaterial wireframe color='green' />
    </mesh>
  )
}

const Ball = () => {
  const [ref]: any[] = useSphere(() => ({
    mass: 1,
    position: [0, 20, 0],
    args: [1]
  }))

  return (
    <mesh castShadow receiveShadow ref={ref}>
      <sphereGeometry args={[1, 64, 64]} />
      <meshLambertMaterial color='white' />
    </mesh>
  )
}

const Plane = (props: any) => {
  const [ref]: any[] = usePlane(() => ({ type: 'Static', ...props }))

  return (
    <mesh ref={ref} receiveShadow>
      <planeGeometry args={[100, 100]} />
      <shadowMaterial color='#171717' />
    </mesh>
  )
} 

const Example = ({ children }: any) => {
  const [bg, setbg] = useState('#171720')
  return (
    <>
      { children }
      <Canvas shadows dpr={[1, 2]} camera={{ position: [-10, 15, 5], fov: 50 }} >
        <OrbitControls />
        <fog attach='fog' args={[bg, 10, 50]} />
        <color attach='background' args={[bg]} />
        <ambientLight intensity={0.1} />
        <spotLight position={[10, 10, 10]} angle={0.5} intensity={1} castShadow penumbra={1} />
        <Physics>
          <BoxTrigger
            onCollide={(e: any) => {
              console.log('Collision event on BoxTrigger', e)
              setbg('#fe4365')
            }}
            position={[0, 5, 0]}
            size={[4, 1, 4]}
          />
          <Ball />
          <Plane rotation={[-Math.PI/2, 0, 0]} />
        </Physics>
      </Canvas>
    </>
  )
}

export default Example